.home-page{
    overflow: hidden;
}

.home-page__welcome-slider {
    height: 100vh;
    min-height: 700px;

    .carousel-root {
        height: 100%;
    }
    .carousel-slider {
        height: 100%;
    }
}


.home-page__filter-section {
    margin-top: -100px;
    z-index: 100;
    position: relative;
}


.home-page__explore-categories {
    padding-top: 90px;
    text-align: center;
}

.home-page__featured-properties {
    background-color: #f7f7f7;
    text-align: center;
    padding-top: 90px;
}

.home-page__video-section {
    width: 100%;
}



.home-page__clients-section {
    background-color: #f7f7f7;
    text-align: center;
    padding-top: 120px;
}



.home-page__agents-section {
    background: linear-gradient(to right, #050d21 65%, #fff 35%);
    padding: 80px 20px 50px;
}
.home-page__agents-cards{

    .section-title {
        margin-left: 70px;
    }

    .section-title__title {
        color: #fff;
    }
}
.home-page__agents {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 25px;
}


.home-page__blog-section {
    background-color: #f7f7f7;
    text-align: center;
    padding: 80px 20px;
}
.home-page__blog-cols {
    padding-top: 50px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 25px;
}


.home-page__gallery-section {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
}
.home-page__gallery-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 1 / 1;
}


@media (max-width: 1280px) {
    .home-page__filter-section {
        margin-top: -60px;
    }
    .home-page__agents {
        .agent-card__image {
            height: 250px;
        }
        .agent-card__listing {
            padding: 20px 15px 8px;
            font-size: 12px;
        }
        .agent-card__name {
            padding: 0 15px 10px;
            font-size: 18px;
        }
        .agent-card__social-links {
            padding: 0 15px 15px;
        }
    }

    .home-page__blog-cols {
        .blog-card__text-content {
            padding: 15px;
        }
        .blog-card__info {
            font-size: 11px;
        }
        .blog-card__title {
            font-size: 17px;
        }
        .blog-card__text {
            font-size: 13px;
        }
    }
}

@media (max-width: 1024px) {
    .home-page__agents {
        grid-template-columns: repeat(auto-fill, minmax(250px, 300px));
        justify-content: center;
        grid-row-gap: 20px;
    }
    .home-page__blog-cols {
        grid-template-columns: repeat(auto-fill, minmax(250px, 300px));
        grid-row-gap: 20px;
        justify-content: center;
    }
    .home-page__gallery-section {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 768px) {
    .home-page__gallery-section {
        grid-template-columns: 1fr 1fr;
    }
}