.agents-page {
    overflow: hidden;
    background: #f7f7f7;
}
.agents-page__agents {
    padding: 80px 20px;
    display: grid;
    grid-template-columns: repeat(4, minmax(280px, 1fr));
    grid-column-gap: 20px;
    grid-row-gap: 40px;
}

@media (max-width: 1280px) {
    .agents-page__agents {
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    }
}