.properties-page {
    overflow: hidden;
    background: #f7f7f7;
}
.properties-page__options-block {
    margin-top: -100px;
}
.properties-page__properties {
    padding: 80px 20px;
    display: grid;
    grid-template-columns: repeat(4, 24%);
    grid-column-gap: 20px;
    grid-row-gap: 40px;

    .property-card-link {
        margin: 5px;
        display: block;
    }
}
.properties-page__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 100px;
}
.properties-page__loader {
    padding: 80px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}


@media (max-width: 1280px) {
    .properties-page__properties {
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
        justify-content: center;
    }
}
